.button {
    width: 100px;
    height: 50px;
    border: none;
    border-radius: 5px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    color: white;
  

    &-project {
        background-color: $orchid-light;
        &:hover{
            background-color: lighten($orchid-light, 10);
        }
    }
    &-submit{
        background-color: $bk-grey;
        &:hover{
            background-color: lighten($orchid-light, 10);
        }
    }


}